/**
 * Universal finder
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/finder/finder
 * @author Web Development
 * @copyright City, University of London 2019
 */

import React from 'react';
import PropTypes from 'prop-types';
import Query from './components/query/finder__query';
import Filters from './components/filters/finder__filtersmobile';
import Results from './components/results/finder__results';
import useLogicWrapper from './logic/logic';

/**
 * Launch the universal Finder.
 *
 * @param {object} props The JSON configuration file for the Finder.
 * @return {object} The React component to render.
 */
function Finder(props) {
    const {
        query,
        funnelbackResponse,
        updating,
        updater,
        clear,
        hasMounted,
        matrixState,
    } = useLogicWrapper(
        props.config,
        props.initialResults || {},
        props.initialQuery,
        props.element
    );

    const queryElement =
        props.config.noQuery && props.config.noQuery === true ? null : (
            <Query
                config={props.config}
                query={query}
                update={updater}
                updating={updating}
            />
        );

    return (
        <form
            className={
                props.config.facetLabels.length > 0
                    ? 'finder'
                    : 'finder finder--nofilters'
            }
            onSubmit={(e) => {
                e.preventDefault();
            }}
        >
            {queryElement}

            {props.config.facetLabels.length > 0 && (
                <React.Fragment>
                    <Filters
                        config={props.config}
                        query={query}
                        response={funnelbackResponse}
                        update={updater}
                        updating={updating}
                        summariseAs={props.config.summariseAs}
                        clear={clear}
                        mobile={true}
                        hasMounted={hasMounted}
                        matrixState={matrixState}
                    />
                </React.Fragment>
            )}
            <Results
                clear={clear}
                config={props.config}
                query={query}
                response={funnelbackResponse}
                summariseAs={props.config.summariseAs}
                type={props.config.resultCard}
                update={updater}
                updating={updating}
            />
        </form>
    );
}

Finder.propTypes = {
    config: PropTypes.object,
    element: PropTypes.object,
    initialQuery: PropTypes.object,
    initialResults: PropTypes.object,
};

export default Finder;
