/**
 * custom hook to check if react has mounted
 *
 *
 * @module patterns/finder/finder
 * @author Web Development
 * @copyright City, University of London 2019
 */

import { useState, useEffect } from 'react';
import useHasMounted from './has-mounted';
import replaceHistory from './replace-history';
import { getNonFBParams, getFacetParams } from './url-params';
import { reduceMotion } from '../../../util';
import { find } from '../funnelback';
import scroll from 'zenscroll';

function useLogicWrapper(config, results, matrixQuery, element) {
    const oneSecond = 1000,
        scrollDuration = reduceMotion() ? 0 : oneSecond,
        screenOffsetRatio = 10;
    /**
     * Check if react has mounted
     */
    const hasMounted = useHasMounted();
    // check if we in Matrix
    const matrixState = !hasMounted && matrixQuery ? true : false;
    const params = matrixState
            ? matrixQuery
            : new URLSearchParams(window.location.search),
        initialResults = results || {};
    const nonFBParams = config.facetLabels.filter(
        (paramFacet) => paramFacet.nonFBParam
    );
    /**
     * initial state for the Funnelback query, taken from URL parameters and
     * configuration
     **/
    const initialQuery = {
        collection: config.collection,
        facets:
            config.facetLabels.length > 0 && params
                ? getFacetParams(config.facetLabels, params, matrixState)
                : {},
        parameters:
            nonFBParams.length > 0 && params
                ? getNonFBParams(config.facetLabels, params, matrixState)
                : {},
        fixedFacets: config.fixedFacets,
        fixedParameters: config.fixParameters ? config.fixParameters : [],
        interacted: false,
        misspelling: null,
        numRanks:
            hasMounted && params.get('num_ranks')
                ? params.get('num_ranks')
                : config.numRanks,
        query:
            !matrixState && params.get('query')
                ? params.get('query')
                : params && params.query
                ? params.query
                : '',
        sortType:
            !matrixState && params.get('query')
                ? ''
                : !matrixState && params.get('sort')
                ? params.get('sort')
                : config.sort[0].type,
        startRank:
            !matrixState && params.get('start_rank')
                ? params.get('start_rank')
                : params && params.start_rank
                ? params.start_rank
                : 1,
    };

    /**
     * Dummy, empty Funnelback response object for initial state.
     */
    const initialResponse =
        Object.keys(initialResults).length > 0
            ? Object.freeze(results)
            : Object.freeze({
                  bestBets: [],
                  extraSearches: {},
                  facets: [],
                  results: [],
                  spell: null,
                  summary: {
                      currEnd: 0,
                      currStart: 0,
                      numRanks: 0,
                      totalMatching: 0,
                  },
              });

    // State objects for the Funnelback query and response
    const [query, setQuery] = useState(initialQuery);
    const [funnelbackResponse, setResponse] = useState(initialResponse);

    // Boolean to indicate when a query is in progress
    const [updating, setUpdating] = useState(
        Object.keys(initialResults).length > 0 ? false : true
    );

    // Request token from the Funnelback request object, so we can cancel if
    // another request is triggered by the user
    const [call, setCall] = useState({
        cancel: () => {},
    });

    // useEffect doesn't deep inspect objects, so we need an additional, plain
    // state variable to indicate that the query state has changed and the
    // component should render
    // the value isn't important, it's just easy to toggle a bool back and forth
    const [update, setUpdate] = useState(false);

    // Retrieve Funnelback results
    useEffect(() => {
        // preserve the state
        replaceHistory(
            query.query,
            query.startRank,
            query.facets,
            query.parameters,
            query.sortType,
            config.facetLabels,
            config.sort[0].type,
            hasMounted
        );

        // indicate a request is in progress
        setUpdating(true);

        query.interacted &&
            scroll.center(
                element.querySelector('.finder__results'),
                scrollDuration,
                -window.innerHeight / screenOffsetRatio
            );

        /**
         * cancel any request already in progress
         *
         * async requests can return out of order
         */
        call.cancel();

        // make a new, asynchronous request to Funnelback
        const [request, requestToken] = find(
            query.collection,
            query.fixedFacets,
            query.fixedParameters,
            query.query,
            query.sortType,
            query.startRank,
            query.numRanks,
            query.facets,
            query.parameters
        );

        // save the requestToken
        setCall({
            cancel: () => {
                requestToken.cancel();
            },
        });

        // when the response from Funnelback arrives,
        // update the results and display them
        request
            .then((data) => {
                setResponse(data);
                setUpdating(false);
            })
            .then(() => {
                query.interacted &&
                    scroll.center(
                        element.querySelector('.finder__results h2'),
                        scrollDuration
                    );
            })
            .catch(() => {
                setResponse(initialResponse);
                setUpdating(false);
            });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [update]);

    // update props so child components can update the query
    const updater = {
        query: (newQuery) => setQuery(newQuery),
        results: (newUpdate) => setUpdate(newUpdate),
        updateState: update,
    };

    /**
     * @param  {boolean} resetSort - Reset the sort order too?
     */
    const clear = (resetSort) => {
        const newQuery = query;

        call.cancel();

        newQuery.sortType = resetSort ? config.sort[0].type : newQuery.sortType;
        newQuery.facets = {};
        newQuery.parameters = {};
        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        setQuery(newQuery);
        setUpdate(!update);
    };

    return {
        query,
        funnelbackResponse,
        updating,
        updater,
        clear,
        hasMounted,
        matrixState,
    };
}

export default useLogicWrapper;
