import React from 'react';
import PropTypes from 'prop-types';

function Finder__hiddenInput(props) {
    return (
        <input
            type="hidden"
            id={props.name}
            name={props.name}
            value={props.value}
        />
    );
}

Finder__hiddenInput.propTypes = {
    name: PropTypes.string,
    value: PropTypes.string,
};

export default Finder__hiddenInput;
